@font-face {
  font-family: "wcag-icons";
  src: url("fonts/wcag-icons.ttf?bfvu5y") format("truetype"),
    url("fonts/wcag-icons.woff?bfvu5y") format("woff"),
    url("fonts/wcag-icons.svg?bfvu5y#wcag-icons") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="wcag-icon-"],
[class*=" wcag-icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "wcag-icons" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

